import { Component, OnInit, OnChanges, Input } from '@angular/core';

import { Coracao } from "../shared/coracao.model";

@Component({
  selector: 'app-tentativas',
  templateUrl: './tentativas.component.html',
  styleUrls: ['./tentativas.component.css']
})
export class TentativasComponent implements OnInit, OnChanges {

   //Property Binding (Afeta atributos dos elementos HTML, definindo-os atravÃ©s de variÃ¡veis)
   //public coracaoVazio: string = "../assets/coracao_vazio.png";
   //public coracaoCheio: string = "../assets/coracao_cheio.png";
   @Input() public tentativas: number;

   public coracoes: Coracao[] = [
     //No constructor existem 3 parametros porem dois jÃ¡ estÃ£o definidos como default,
     //entÃ£o, posso passar apenas o que falta, mas poderia tambÃ©m sobrescrever os outros
      new Coracao(true), new Coracao(true), new Coracao(true)
   ];
  
  constructor() {
    
  }

  //Ciclo de Vida - Metodo executado durante o processo de decoraÃ§Ã£o (input dos dados) de um component pai para filho
  ngOnChanges(){
    //Nesse caso sempre que o valor de tentativas for atualizado essa informaÃ§Ã£o irÃ¡ aparecer
    //console.log(this.tentativas);

    //Se o total de tentativas for diferente do tamanho do array de tentativas
    //entÃ£o, diminuo do total de elementos do array a quantidade de tentativas menos 1 porque o array comeÃ§a de zero.
    if(this.tentativas !== this.coracoes.length){
      let indice = this.coracoes.length - this.tentativas;
      this.coracoes[indice - 1].cheio = false;
    }
  }

  //Esse metodo Ã© executado apenas uma unica vez quando o component Ã© instanciado
  ngOnInit() {
    //Essa instruÃ§Ã£o Ã© inserida aqui para que o processo de decoraÃ§Ã£o de variavel
    //de tentativas seja executado, o que nÃ£o acontece dentro do constructor, pois,
    //as propriedades do objeto sÃ³ sÃ£o alteradas no metodo ngOnChangs depois do constructor.
    //console.log(this.tentativas);
  }
}



